.error-container {
  display: flex;
  flex-direction: column;
}

.error-404 {
  color: #485fc7;
  font-size: 18rem;
  font-weight: 700;
  text-align: center;
  margin-bottom: 10px;
  margin-top: 50px;
}

.error-message {
  color: #485fc7;
  font-size: 2.25rem;
  font-weight: 500;
  text-align: center;
  margin: 0px;
}

.link-home {
  color: #485fc7;
  font-size: 1.2rem;
  font-weight: 500;
  display: flex;
  justify-content: center;
  margin-top: 150px;
  margin-bottom: 100px;
  text-decoration: underline;
}

@media (max-width: 767px) {
  .error-404 {
    margin-top: 175px;
    font-size: 96px;
  }

  .error-message {
    font-size: 18px;
    width: 66%;
    margin: auto;
    font-weight: 400;
  }

  .link-home {
    font-size: 14px;
    margin-top: 133px;
    font-weight: 400;
  }
}
