.carrousel_container {


  height: 415px;
  position: relative;
  margin: 0;
}

.slide {
  
  width: 100%;
  height: 100%;
  border-radius: 25px;
  object-fit: contain;
}

.arrow_container {
  height: 100%;
  width: 100%;
  margin: 0 auto;
  position: absolute;
  bottom: 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.prev {
  margin-left: 10px;
  cursor: pointer;
}

.next {
  margin-right: 10px;
  cursor: pointer;
}

.number {
  font-size: 1.25rem;
  color: #ffffff;
  position: absolute;
  bottom: 10px;
  display: flex;
  justify-content: center;
  width: 100%;
  letter-spacing: -2.5px;
}

.arrow_container {
  height: 100%;
  width: 100%;
  margin: 0 auto;
  position: absolute;
  bottom: 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

button {
  border: none;
  background-color: rgba(255, 255, 255, 0);
}

@media (max-width: 767px) {
  .carrousel_container {
    height: 255px;
  }

  .slide {
    border-radius: 10px;
  }

  .prev,
  .next {
    height: 20px;
  }

  .prev img,
  .next img {
    height: 20px;
  }

  .prev {
    margin-left: 5px;
  }

  .next {
    margin-right: 5px;
  }

  .number {
    display: none;
  }
}
